.menu {
  background: $menuColor;
  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0;
  }

  &__top {
    margin-bottom: 40px;
  }

  &__list {
    margin: 0 0 40px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;

    text-decoration: none;
    font-weight: 600;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    border-bottom: 1px solid transparent;
    color: $colorBlack;
    text-transform: uppercase;
    transition: border-color $transitions;

    &:hover {
      border-color: $colorBlack;
    }
  }

  &__call {
    display: block;
    width: max-content;

    font-weight: 600;
    text-transform: uppercase;
    text-decoration: none;
    color: $colorBlack;

    &--phone {
      font-size: 16px;
      line-height: 27px;
      margin-bottom: 16px;
    }

    &--action {
      border-bottom: 1px solid $colorBlack;
      padding-bottom: 8px;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
    }
  }
}
